.plans {
  background-color: #fff;
  padding-top: 50px;
  padding-bottom: 20px;
  overflow: hidden;

  @media #{$lg} {
    padding-top: 200px;
    padding-bottom: 110px;
  }

  &_wrap {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 20px;

    @media #{$lg} {
      max-width: 1120px;
      padding: 0;
    }
  }

  .headline:before {
    content: 'Plans.';
  }

  &_menu {
    padding-top: 20px;
    padding-bottom: 40px;

    @media #{$lg} {
      padding-top: 120px;
      padding-bottom: 60px;
    }
  }

  &-cards {
    display: flex;
    align-items: stretch;
    align-content: stretch;
    flex-wrap: wrap;
  }
}

.plans-card {
  flex-basis: 100%;
  min-width: 100%;
  border: 2px solid rgba(68, 88, 107, .07);
  font-family: 'Futura-Medium';
  margin-bottom: 20px;

  @media #{$lg} {
    margin-bottom: 40px;
    flex-basis: 33.33333333%;
    min-width: 33.33333333%;
    border-right-color: transparent;

    &:last-child { margin-bottom: 40px; }
  }

  &:last-child,
  &:nth-child(3n) {
    border-right-color: rgba(68, 88, 107, .07);
  }

  &:last-child { margin-bottom: 0; }

  &_box {
    height: 100%;
    align-items: stretch;
    align-content: stretch;
    display: flex;
    flex-direction: column;
    padding: 25px 30px;

    @media #{$lg} {
      padding: 25px 50px;
    }
  }

  &_headline {
    margin-bottom: 25px;
    text-align: center;
  }

  &_name {
    margin-bottom: 10px;
    font-size: 24px;
    line-height: 24px;
    color: #1C2030;
    letter-spacing: 0;
    text-align: center;
  }

  &_price {
    text-align: center;
    font-size: 24px;
    line-height: 24px;
    color: #D91AA2;
    letter-spacing: 0;
  }

  &_body {
    height: 100%;
    font-size: 15px;
    color: #1C2030;
    letter-spacing: 0;
    text-align: justify;
    align-items: stretch;
    align-content: stretch;
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;

    p {
      opacity: .7;
      font-size: 15px;
      color: #1C2030;
      letter-spacing: 0;
      text-align: justify;
      margin-bottom: 0;
    }

    .more-link {
      padding-top: 30px;
      margin-top: auto;
    }
  }

  &.is-select {
    background: #D91AA2;
    box-shadow: 0 4px 6px 0 rgba(217, 26, 162, .5);
    color: #ffffff;

    .plans-card_name,
    .plans-card_price,
    .plans-card_body,
    .plans-card_body > p,
    .more-link .button-light-link {
      color: #ffffff;
    }

    .more-link {
      opacity: .6;

      &:hover { opacity: 1; }
    }

    .plans-card_body > p { opacity: 1; }

    .form-checkbox {
      color: #ffffff;

      .checkbox-custom {
        border-color: #ffffff;

        &:before {
          background-color: #fff;
        }
      }
    }
  }
}
