// 
a {
  transition: .3s ease;
  text-decoration: none;
  color: $link-color;
  border-bottom: 1px solid;

  &:focus {
    outline: none;
  }

  &:hover,
  &.hover {
    color: $link-hover-color;
    transition: .2s ease;
  }

  /*
  &:visited {
    color: $link-visited-color;

    &:hover {
      color: $link-hover-color;
      transition: .2s ease;
    }
  }*/

  &:active,
  &.active {
    color: $link-active-color;
  }
}

button[disabled=disabled],
button:disabled,
.button:disabled,
.button.disabled {
  cursor: not-allowed;
  //pointer-events: none;
  color: rgb(173, 177, 185);
  background-color: rgb(247, 247, 248);
  border-color: rgb(214, 218, 221);
}

.button {
  background-color: transparent;
  border: 1px solid transparent;
  border-radius: 0;
  padding: 20px 42px 19px 42px;
  color: rgb(255, 255, 255);
  font-family: 'Futura-Medium';
  font-size: 16px;
  letter-spacing: 0;
  line-height: 18px;
  white-space: nowrap;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  transition: all .2s ease;
  vertical-align: middle;
  position: relative;
  overflow: hidden;
  cursor: pointer;

  &-green {
    background-color: #00B96E;

    &:hover,
    &.hover {
      background-color: darken(#00B96E, 10%);
      color: #ffffff;
    }

    &:focus,
    &.focus {
      background-color: darken(#00B96E, 15%);
      color: #ffffff;
    }

    &[disabled=disabled],
    &:disabled,
    &:disabled,
    &.disabled {
      color: rgb(255, 255, 255);
      background-color: rgb(214, 217, 220);
      border-color: rgb(214, 217, 220);
    }
  }

  &-outline {
    border-width: 2px;
    border-color: #D91AA2;
    padding-left: 50px;
    padding-right: 50px;

    &:hover {
      background-color: darken(#D91AA2, 10%);
      border-color: darken(#D91AA2, 10%);
      color: #ffffff;
    }
  }

  &-pink {
    background-color: #D91AA2;

    &:hover {
      background-color: darken(#D91AA2, 15%);
      color: #ffffff;
    }
  }

  &-with-icon {
    display: flex;
    align-items: center;
    align-content: center;

    .button_icon { margin-left: 8px; }
  }

  &-light-link {
    font-size: 15px;
    line-height: 15px;
    color: #7E7E7E;
    letter-spacing: 0;
    font-weight: 200;
    padding: 0;
  }
}
